"use client"

import React, { useEffect, useState } from 'react'
import './Quantity.scss'
import { useQuantity } from '@/app/Context/QuantityContext/QuantityContext';
function Quantity({productId}) {

    const {getQuanityForProduct , setQuanityForProduct} = useQuantity();
    const [quantity , setQuantity] = useState(getQuanityForProduct(productId));
    
    useEffect(()=>{
        setQuantity(getQuanityForProduct(productId))
    },[productId,getQuanityForProduct])

    const handlePlus = () =>{
        const newQuantity = quantity + 1;
        setQuantity(newQuantity);
        setQuanityForProduct(productId,newQuantity);
    };
    const handleMinus = () =>{
        if (quantity > 0){
            const newQuantity = quantity - 1;
            setQuantity(newQuantity);
            setQuanityForProduct(productId, newQuantity);
        }
    }

  return (
    <div className='quantity'>
        <span className='label_'>Quantity</span>
        <span className='quantity_field'>
            <label className='qnty_btn minus' onClick={handleMinus}>-</label>
            <input type='number' className='qnty_txt' value={quantity} readOnly />
            <label className='qnty_btn plus' onClick={handlePlus}>+</label>
        </span>
    </div>
  )
}

export default Quantity
